.psv-settings-item-label {
  flex: 1;
  font-weight: bold;
}
.psv-settings-item-value {
  flex: none;
}
.psv-settings-item-icon {
  flex: none;
  height: 1em;
  width: 1em;
}
.psv-settings-item-icon svg {
  width: 1em;
  height: 1em;
}
.psv-settings-item-icon:first-child {
  margin-right: 1em;
}
.psv-settings-item-icon:last-child {
  margin-left: 1em;
}
.psv-settings-item--header {
  border-bottom: 1px solid currentcolor;
  margin-bottom: 1em;
}
.psv-settings-item--header svg {
  transform: scaleX(-1);
}

.psv-settings-badge {
  position: absolute;
  top: 10%;
  right: 10%;
  border-radius: 0.2em;
  padding: 0.2em;
  background: #111;
  color: white;
  font: 10px/0.9 monospace;
}

.psv-settings-menu {
  justify-content: flex-end;
}
.psv-settings-menu .psv-panel-menu-list {
  flex: none;
  margin-bottom: 20px;
}